ntainer\">\r\n                <div class=\"username_container\">\r\n                  <h3 v-if=\"editor.layout.profile_details.name\" class=\"name\" :style=\"{ color: editor.styles.header_text }\">{{ profile.name }}</h3>\r\n                </div>\r\n                <div class=\"username_container\">\r\n                  <h3 v-if=\"editor.layout.profile_details.username\" class=\"username\" :style=\"{ color: editor.styles.header_text }\">\r\n                    {{ profile.username }}\r\n                  </h3>\r\n                </div>\r\n              </div>\r\n            </div>\r\n            <div class=\"profile_insights_container\">\r\n              <p v-if=\"editor.layout.profile_details.post_count\" class=\"insight_count_tag\" :style=\"{ color: editor.styles.header_text }\">\r\n                <strong class=\"post_count\" :style=\"{ color: editor.styles.header_text }\">{{ profile.media_count }}</strong>\r\n                posts\r\n              </p>\r\n              <p v-if=\"editor.layout.profile_details.followers\" class=\"insight_count_tag\" :style=\"{ color: editor.styles.header_text }\">\r\n                <strong class=\"followers_count\" :style=\"{ color: editor.styles.header_text }\">{{ profile.followers_count }}</strong>\r\n                followers\r\n              </p>\r\n              <p v-if=\"editor.layout.profile_details.following\" class=\"insight_count_tag\" :style=\"{ color: editor.styles.header_text }\">\r\n                <strong class=\"following_count\" :style=\"{ color: editor.styles.header_text }\">{{ profile.follows_count }}</strong>\r\n                following\r\n              </p>\r\n            </div>\r\n            <div class=\"follow_button_container\">\r\n              <button v-if=\"editor.layout.profile_details.follow_button\" class=\"follow_button\"\r\n                      @click=\"redirectToProfile\"\r\n                      :style=\"{ backgroundColor: editor.styles.follow_button_bg, color: editor.styles.follow_button_text }\">\r\n                <i class=\"uil uil-instagram\"></i> Follow\r\n              </button>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </div>\r\n      <div v-if=\"editor.layout.layoutStyle === 'grid'\" class=\"posts_container grid_layout_posts_container\"\r\n           :style=\"{\r\n              gridTemplateColumns: `repeat(${editor.layout.columns}, 1fr)`,\r\n              gap: editor.layout.padding + 'px',\r\n            }\">\r\n        <div v-for=\"post in filteredPosts\" :key=\"post.id\" class=\"post image_card\" @click=\"post_action(post)\">\r\n          <div class=\"media_wrapper\">\r\n            <i class=\"media_icon\"\r\n               :class=\"{'uil uil-play': post.media_type === 'VIDEO', 'uil uil-layers': post.media_type == 'CAROUSEL_ALBUM' }\"></i>\r\n            <img v-if=\"post.media_type === 'IMAGE' && post.media_product_type === 'FEED'\" :src=\"post.media_url\"\r\n                 class=\"post_image\" alt=\"Post image\">\r\n            <img v-else-if=\"post.media_type === 'IMAGE'\" :src=\"post.media_url\" class=\"post_image\"\r\n                 alt=\"Post image\">\r\n            <img v-else-if=\"post.media_type === 'CAROUSEL_ALBUM'\" :src=\"post.media_url\" class=\"post_image\"\r\n                 alt=\"Post image\">\r\n            <video v-if=\"post.media_type === 'VIDEO' && post.media_product_type === 'REELS'\"\r\n                   :src=\"post.media_url\" class=\"post_image\"></video>\r\n            <video v-else-if=\"post.media_type === 'VIDEO'\" :src=\"post.media_url\" class=\"post_image\"></video>\r\n          </div>\r\n          <div class=\"post_insights_container\">\r\n          <span class=\"d-flex gap-5\">\r\n            <p class=\"post_like\">\r\n              <i class=\"uil uil-heart-alt\"></i>{{ post.like_count }}\r\n            </p>\r\n            <p class=\"post_like ml-3\">\r\n              <i class=\"uil uil-comment\"></i>{{ post.comments_count }}\r\n            </p>\r\n          </span>\r\n          </div>\r\n        </div>\r\n        <div v-for=\"post in expandedPosts\" :key=\"post.id\" class=\"post expanded\">\r\n          <div class=\"post_header justify-content-between align-items-center\">\r\n            <div class=\"d-flex justify-content-start align-items-center\">\r\n              <div v-if=\"editor.post_style.post_details.profile_image\" class=\"profile_image_container\">\r\n                <img\r\n                    :src=\"(profile.profile_picture_url) ? profile.profile_picture_url : '/images/widget-layouts/profile.jpg'\"\r\n                    :alt=\"profile.name\" class=\"profile_image\">\r\n              </div>\r\n              <div v-if=\"editor.post_style.post_details.username\" class=\"post_username_container mx-1\">\r\n                <h3 class=\"username\">{{ profile.username }}</h3>\r\n              </div>\r\n            </div>\r\n            <div class=\"font-size-24 mx-2\">\r\n              <a :href=\"'https://instagram.com/' + profile.username\" target=\"_blank\">\r\n                <i class=\"uil uil-instagram instagram_icon\"></i>\r\n              </a>\r\n            </div>\r\n          </div>\r\n          <div class=\"post_image_container\" @click=\"post_action(post)\">\r\n            <div class=\"media_wrapper\">\r\n              <i class=\"media_icon\"\r\n                 :class=\"{'uil uil-play': post.media_type === 'VIDEO', 'uil uil-layers': post.media_type == 'CAROUSEL_ALBUM' }\"></i>\r\n              <img v-if=\"post.media_type === 'IMAGE' && post.media_product_type === 'FEED'\"\r\n                   :src=\"post.media_url\"\r\n                   class=\"post_image\" alt=\"Post image\">\r\n              <img v-else-if=\"post.media_type === 'IMAGE'\" :src=\"post.media_url\" class=\"post_image\"\r\n                   alt=\"Post image\">\r\n              <img v-else-if=\"post.media_type === 'CAROUSEL_ALBUM'\" :src=\"post.media_url\" class=\"post_image\"\r\n                   alt=\"Post image\">\r\n              <video v-if=\"post.media_type === 'VIDEO' && post.media_product_type === 'REELS'\"\r\n                     :src=\"post.media_url\" class=\"post_image\"></video>\r\n              <video v-else-if=\"post.media_type === 'VIDEO'\" :src=\"post.media_url\"\r\n                     class=\"post_image\"></video>\r\n            </div>\r\n          </div>\r\n          <div class=\"post_footer\" @click=\"post_action(post)\">\r\n            <div class=\"post_insights_container\">\r\n            <span class=\"d-flex gap-5\">\r\n              <p v-if=\"editor.post_style.post_details.likes_count\" class=\"post_like\">\r\n                <i class=\"uil uil-heart-alt\"></i>{{ post.like_count }}\r\n              </p>\r\n              <p v-if=\"editor.post_style.post_details.comments_count\" class=\"post_like ml-2\">\r\n                <i class=\"uil uil-comment\"></i>{{ post.comments_count }}\r\n              </p>\r\n            </span>\r\n            </div>\r\n            <p v-if=\"editor.post_style.post_details.caption\" class=\"post_caption\">{{ post.caption }}</p>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport axios from \"axios\";\r\naxios.defaults.baseURL = (process.env.NODE_ENV === 'production' ? window.location.origin.includes('staging.') ? 'http://127.0.0.1:8000/' : 'http://127.0.0.1:8000/' : 'http://127.0.0.1:8000/') + 'api/v1';\r\nexport default {\r\n  name: \"InstagramWidget\",\r\n  data() {\r\n    return {\r\n      profileFetchState: '',\r\n      profileFetchMessage: '',\r\n      profile: {\r\n        username: '',\r\n        name: '',\r\n        profile_picture_url: '',\r\n        followers_count: '',\r\n        follows_count: '',\r\n        media_count: '',\r\n      },\r\n      tokens: {\r\n        accessToken: '',\r\n        igID: ''\r\n      },\r\n      posts: [{\r\n        'id': '',\r\n        'like_count': '',\r\n        'comments_count': '',\r\n        'caption': '',\r\n        'media_url': '',\r\n      }],\r\n      posts_type: 'profile',\r\n      widgetId: 0,\r\n      new_widget_title: '',\r\n      editor: {\r\n        source: {\r\n          username: 'elonreevmusk',\r\n          type: 'username',\r\n        },\r\n        layout: {\r\n          layoutStyle: 'grid',\r\n          rows: 3,\r\n          columns: 3,\r\n          show_profile_details: true,\r\n          profile_details: {\r\n            profile_image: true,\r\n            username: true,\r\n            name: true,\r\n            follow_button: true,\r\n            post_count: true,\r\n            followers: true,\r\n            following: true,\r\n          },\r\n          padding: 5,\r\n        },\r\n        post_style: {\r\n          post_style: 'image_card',\r\n          post_details: {\r\n            profile_image: true,\r\n            username: true,\r\n            likes_count: true,\r\n            comment_count: true,\r\n            caption: true,\r\n          },\r\n          post_action: 'instagram'\r\n        },\r\n        editing: 'layout',\r\n        styles: {\r\n          theme: 'light',\r\n          header_bg: '#ffffff',\r\n          header_text: '#000000',\r\n          follow_button_bg: '#0081ff',\r\n          follow_button_text: '#ffffff',\r\n          load_more_button_bg: '#0081ff',\r\n          load_more_button_text: '#ffffff',\r\n\r\n        }\r\n      },\r\n      gettingWidget: false,\r\n      additionalPostsToShow: 0,\r\n      widgets: [],\r\n      selectedWidget: null,\r\n    }\r\n  },\r\n\r\n  computed: {\r\n    widgetList() {\r\n      let widgets = this.$store.getters['igwidget/all'];\r\n      console.log(widgets);\r\n      return this.$store.getters['igwidget/all'];\r\n    },\r\n    visiblePostCount() {\r\n      return (this.editor.layout.columns * this.editor.layout.rows) + this.additionalPostsToShow;\r\n    },\r\n    visiblePosts() {\r\n      const maxPosts = (this.editor.layout.columns * this.editor.layout.rows) + this.additionalPostsToShow;\r\n      const filteredPosts = this.posts.filter(post => post.media_url);\r\n      return filteredPosts.slice(0, maxPosts);\r\n    },\r\n    showLoadMore() {\r\n      console.log(this.visiblePostCount)\r\n      console.log(this.posts.length)\r\n      return this.visiblePostCount < this.posts.length;\r\n    },\r\n    filteredPosts() {\r\n      return this.posts.filter(post => {\r\n        return this.editor.post_style.post_style === 'image_card' && post.media_url;\r\n      });\r\n    },\r\n    expandedPosts() {\r\n      return this.posts.filter(post => {\r\n        return this.editor.post_style.post_style === 'expanded' && post.media_url;\r\n      });\r\n    }\r\n  },\r\n\r\n  mounted() {\r\n    this.getWidget(false);\r\n  },\r\n\r\n  watch: {\r\n    reload: (val) => {\r\n      this.getWidget(false);\r\n      console.log(val)\r\n    },\r\n    'editor.styles.theme': function (newTheme) {\r\n      if (newTheme === 'dark') {\r\n        this.editor.styles.header_bg = '#1b1b1b';\r\n        this.editor.styles.header_text = '#ffffff';\r\n        this.editor.styles.follow_button_bg = '#333333';\r\n        this.editor.styles.follow_button_text = '#ffffff';\r\n        this.editor.styles.load_more_button_bg = '#333333';\r\n        this.editor.styles.load_more_button_text = '#ffffff';\r\n      } else if (newTheme === 'light') {\r\n        this.editor.styles.header_bg = '#ffffff';\r\n        this.editor.styles.header_text = '#000000';\r\n        this.editor.styles.follow_button_bg = '#0081ff';\r\n        this.editor.styles.follow_button_text = '#ffffff';\r\n        this.editor.styles.load_more_button_bg = '#0081ff';\r\n        this.editor.styles.load_more_button_text = '#ffffff';\r\n      }\r\n    }\r\n  },\r\n\r\n  methods: {\r\n    loadMore() {\r\n      this.additionalPostsToShow += this.editor.layout.columns;\r\n\r\n      console.log(this.additionalPostsToShow)\r\n    },\r\n\r\n    getWidget(fetchProfile = false) {\r\n      let username = this.editor.source.username ?? 'elonreevmusk';\r\n      this.gettingWidget = true;\r\n      this.profileFetchState = 'fetching';\r\n\r\n      if (fetchProfile) {\r\n        if (this.editor.source.type === 'username') {\r\n          this.$store\r\n              .dispatch('igwidget/getIgWidget', {\r\n                username: username\r\n              })\r\n              .then((res) => {\r\n                if (res.businessDetails) {\r\n                  this.profile = res.businessDetails;\r\n                  this.posts = res.businessDetails.media.data;\r\n                  console.log(this.profile);\r\n                  this.gettingWidget = false;\r\n                  this.profileFetchState = 'success';\r\n                  this.posts_type = 'profile'\r\n                  if (fetchProfile !== true) {\r\n                    this.showWidgetModal();\r\n                  }\r\n                } else {\r\n                  console.log('Invalid Username or You\\'re trying to access a private account');\r\n                  this.gettingWidget = false;\r\n                  this.profileFetchState = 'error';\r\n                  this.profileFetchMessage = 'Invalid Username or You\\'re trying to access a private account'\r\n                }\r\n\r\n              })\r\n              .catch((error) => {\r\n                console.log(error)\r\n                console.log('Invalid Username or You\\'re trying to access a private account');\r\n                this.gettingWidget = false;\r\n                this.profileFetchState = 'error';\r\n                this.profileFetchMessage = 'Invalid Username or You\\'re trying to access a private account'\r\n              });\r\n        }\r\n        else {\r\n          this.$store\r\n              .dispatch('igwidget/searchKeyword', {\r\n                username: username\r\n              })\r\n              .then((res) => {\r\n                console.log(res)\r\n\r\n                this.posts = res;\r\n                this.gettingWidget = false;\r\n                this.profileFetchState = 'success';\r\n                this.posts_type = 'profile'\r\n                this.posts_type = 'hashtag'\r\n                if (fetchProfile !== true) {\r\n                  this.showWidgetModal();\r\n                }\r\n              })\r\n              .catch((error) => {\r\n                console.log(error)\r\n                console.log('Invalid Username or You\\'re trying to access a private account');\r\n                this.gettingWidget = false;\r\n                this.profileFetchState = 'error';\r\n                this.profileFetchMessage = 'Invalid Username or You\\'re trying to access a private account'\r\n              });\r\n        }\r\n      } else {\r\n        console.log(\"Fetching Profile\")\r\n        if (this.editor.source.type === 'username') {\r\n          this.$store\r\n              .dispatch('igwidget/getIgWidget', {\r\n                username: username\r\n              })\r\n              .then((res) => {\r\n                if (res.businessDetails) {\r\n                  this.profile = res.businessDetails;\r\n                  this.posts = res.businessDetails.media.data;\r\n                  console.log(this.profile);\r\n                  this.gettingWidget = false;\r\n                  this.profileFetchState = 'success';\r\n                  this.posts_type = 'profile'\r\n                  if (fetchProfile !== true) {\r\n                    this.showWidgetModal();\r\n                  }\r\n                } else {\r\n                  console.log('Invalid Username or You\\'re trying to access a private account');\r\n                  this.gettingWidget = false;\r\n                  this.profileFetchState = 'error';\r\n                  this.profileFetchMessage = 'Invalid Username or You\\'re trying to access a private account'\r\n                }\r\n\r\n              })\r\n              .catch((error) => {\r\n                console.log(error)\r\n                console.log('Invalid Username or You\\'re trying to access a private account');\r\n                this.gettingWidget = false;\r\n                this.profileFetchState = 'error';\r\n                this.profileFetchMessage = 'Invalid Username or You\\'re trying to access a private account'\r\n              });\r\n        }\r\n        else {\r\n          this.$store\r\n              .dispatch('igwidget/searchKeyword', {\r\n                username: username\r\n              })\r\n              .then((res) => {\r\n                console.log(res)\r\n\r\n                this.posts = res;\r\n                this.gettingWidget = false;\r\n                this.profileFetchState = 'success';\r\n                this.posts_type = 'profile'\r\n                this.posts_type = 'hashtag'\r\n                if (fetchProfile !== true) {\r\n                  this.showWidgetModal();\r\n                }\r\n              })\r\n              .catch((error) => {\r\n                console.log(error)\r\n                console.log('Invalid Username or You\\'re trying to access a private account');\r\n                this.gettingWidget = false;\r\n                this.profileFetchState = 'error';\r\n                this.profileFetchMessage = 'Invalid Username or You\\'re trying to access a private account'\r\n              });\r\n        }\r\n      }\r\n    },\r\n\r\n    post_action(post) {\r\n      if (this.editor.post_style.post_action === 'instagram') {\r\n        window.open(post.permalink, '_blank');\r\n      } else {\r\n        this.showPostModal = true;\r\n        this.$nextTick(() => {\r\n          setTimeout(() => {\r\n            const postId = 'post_' + post.id;\r\n            const postElement = document.getElementById(postId);\r\n            if (postElement) {\r\n              postElement.scrollIntoView({ behavior: 'smooth' });\r\n            }\r\n          }, 300);\r\n        });\r\n      }\r\n    },\r\n\r\n    redirectToProfile() {\r\n      if (this.profile && this.profile.username) {\r\n        // Redirect to user's Instagram profile\r\n        window.open(`https://www.instagram.com/${this.profile.username}`, '_blank');\r\n      }\r\n    },\r\n\r\n  },\r\n}\r\n</script>\r\n\r\n<!--WIdget Styles-->\r\n<style scoped>\r\n\r\n.fetch_loader {\r\n  width: 100%;\r\n  height: 100%;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.customer_select {\r\n  border: 1px solid #8a8a8a;\r\n  border-radius: 4px;\r\n  color: #000;\r\n\r\n  * {\r\n    color: #000;\r\n  }\r\n}\r\n\r\n.source_input {\r\n  width: 100%;\r\n  padding: .5rem;\r\n  border-radius: 4px;\r\n  border: 2px solid #456db9;\r\n  font-size: 1.1rem;\r\n}\r\n\r\n.source_button {\r\n  height: 100%;\r\n  background: #284273;\r\n  color: #fff;\r\n  border: none;\r\n  padding: .5rem;\r\n  font-size: 1.1rem;\r\n}\r\n\r\n.v-select {\r\n  cursor: pointer !important;\r\n\r\n  * {\r\n    cursor: pointer !important;\r\n  }\r\n}\r\n\r\n.layouts_wrapper {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 1rem;\r\n}\r\n\r\n.layout-item {\r\n\r\n  .btn {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    width: fit-content;\r\n\r\n    img {\r\n      display: inline-block;\r\n      width: 5rem;\r\n    }\r\n\r\n    label {\r\n      display: inline-block;\r\n      font-size: 12px;\r\n    }\r\n  }\r\n\r\n}\r\n\r\n.instagram-widget {\r\n  max-height: 93vh;\r\n  overflow-y: scroll;\r\n\r\n  .profile_details_container {\r\n    padding: 2rem 1rem;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    gap: 3rem;\r\n\r\n    .profile_details_left {\r\n      display: flex;\r\n      align-items: center;\r\n      gap: 1rem;\r\n    }\r\n\r\n    .profile_picture_container {\r\n      width: 80px;\r\n      height: 80px;\r\n      border-radius: 50%;\r\n    }\r\n\r\n    .profile_picture {\r\n      width: 100%;\r\n      height: 100%;\r\n      object-fit: cover;\r\n      border-radius: 50%;\r\n    }\r\n\r\n    .profile_names_container {\r\n      justify-self: flex-start;\r\n\r\n  